.services {
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    
  }
  .test{
    width: 50px;
    height: 50px;
    align-items:center ;
    background-color: #898989;
  }
  .services .serviceTitle {
    font-family: "Helvetica Nueu", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
    font-size: 50px;
    padding:50px
  }
  
  .serviceList {
    width: 70vw;
    height: auto;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    place-items: center;
  
  }
  
  /* MENU ITEM STYLING */
  
  .serviceItem {
    border-radius: 15px;
    width: 300px;
    height: 350px;
    margin: 20px;
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.2);
  }
  .serviceItem:hover {
    box-shadow: 0px 3px 15px rgba(0, 0, 0, 0.5);
    transition: 0.3s ease-in;
    cursor: pointer;
  }
  
  .serviceItem div {
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
    width: 100%;
    height: 200px;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  
  .serviceItem h1,
  .serviceItem p {
    margin-left: 20px;
  }

  .no-padding{
    padding: 0;
    border: none;
    background: none;
  }
  
  @media only screen and (max-width: 1300px) {
    .serviceList {
      grid-template-columns: 1fr 1fr;
    }
  }
  
  @media only screen and (max-width: 800px) {
    .serviceList {
      grid-template-columns: 1fr;
    }
  }